@mixin home--gradients {
  .container {
    position: relative;

    .header {
      position: absolute;
      z-index: 10;
    }

    .hero {
      position: relative;
      background-image: radial-gradient(
        circle closest-side, 
        $blue,
        $black
      );
    }

    .publishers {
      background-image: linear-gradient(
        $black,
        $gray-footer
      );
    }

    .advertisers {
      background-image: linear-gradient(
        $gray-footer,
        $blue
      );
    }

    .rich-media {
      background-image: linear-gradient(
        $blue,
        $green
      );
    }

    .everything {
      background-image: linear-gradient(
        $green,
        $green-dark
      );
    }
  }
}
