@mixin web-interface {
  @include section-fitscreen;
  @include colors(white-on-black);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__slides {
    @include web-interface__slides;
  }

  &__description {
    @include web-interface__description;
  }

  .article {
    @include article--centered;
  }
}
