@mixin publishers__mockups {
  display: flex;
  align-items: baseline;
  justify-content: flex-end;

  position: relative;

  .mockup {
    @include mockup;

    position: absolute;
    left: 0;
    top: 0;

    &:first-of-type {
      @include mockup--placeholder($green);
    }

    &:last-of-type {
      @include mockup--placeholder($blue);
    }
  }

  @include publishers__mockups--landscape;
  @include publishers__mockups--portrait;
  @include publishers__mockups--mobile;
}
