@mixin hero {
  @include section-padding;
  @include section-fitscreen;

  .article {
    @include article;
    @include article--centered;

    &__title {
      @include text-style(hero);
    }

    &__subtitle {
      @include colors(gray-format-on-transparent);

      margin: $lem 0;
      letter-spacing: 0.035em;
    }
  }
}
