@mixin contact {
  display: flex;
  flex-direction: column;

  &__title {
    display: flex;

    @include text-style(headline-3);
    padding: $lem / 4 0;
  }

  .ul {
    @include ul;
    @include ul--horizontal;

    justify-content: flex-start;
    @include text-style(footer);

    .li {
      .link {
        @include link(blue-on-white, blue-on-white, blue-on-white);
      }
    }
  }

  .articles {
    @include articles(2);

    .article {
      &__title {
        @include text-style(footer);
      }
    }
  }
}
