@mixin footer-bottom__made-with {
  @include text-centered;
  @include colors(gray-footer-on-white);

  .made-with {
    @include text-style(section-title);

    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    .sprite--made-with-muscle {
      @include sprite($made-with-muscle);
      display: flex;
      margin: 0 $lem / 2;
    }
  }

  .copyright {
    margin-top: $lem / 4;
  }
}
