@mixin publishers__slider {
  flex-basis: 100%;
  align-items: flex-end;
  align-self: flex-end;
  overflow-x: hidden;

  .slider2 {
    @include slider2;

    .slide {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .picture {
        @include border(thin, all);
        border-color: transparent;

        padding: $lem / 4;
        line-height: 0;
      }

      &--active {
        .picture {
          border-color: $green;
        }

        .figcaption {
          @include colors(green-on-transparent);
        }
      }
    }

    .button {
      @include colors(white-on-green);
    }
  }
}
