@mixin publishers__mockups--landscape {
  @include landscape {
    .mockup {
      &:first-of-type {
        width: $lem * 20;
        transform: scale(.7);
        z-index: 1;
      }

      &:last-of-type {
        perspective: 1000px;

        .mockup--browser {
          transform: rotateY(-10deg) translateX($lem * 5);
        }
      }
    }
  }
}
