@mixin slider2__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: absolute;
  width: 100%;

  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .triangle {
      position: relative;

      &--left {
        left: -$lem / 8;
      }

      &--right {
        left: $lem / 8;
      }
    }
  }
}
