@mixin advertisers__mockups {
  display: flex;
  align-items: baseline;
  justify-content: flex-end;

  position: relative;

  .mockup {
    @include mockup;

    position: absolute;
    left: 0;
    top: 0;
    width: 95%;

    &:nth-of-type(1) {
      @include mockup--placeholder($green);
      z-index: 1;
      top: -5%;
      left: -5%;
    }

    &:nth-of-type(2) {
      @include mockup--placeholder($red);
      z-index: 2;
      top: 0%;
      left: 0%;
    }

    &:nth-of-type(3) {
      @include mockup--placeholder($blue);
      z-index: 3;
      top: 5%;
      left: 5%;
    }
  }

  @include advertisers__mockups--landscape;
  @include advertisers__mockups--portrait;
  @include advertisers__mockups--mobile;
}
