

// Fonts
//
// Typefaces and fonts used across the site.
// They are never used directly but set up properly with the `text-style` mixin.
//
// Styleguide fonts


// Roboto
// - The default typeface, sans-serif
// - character set: Latin
// - served from Googlexx
// - styles and weights: Light, Regular, Medium, 


// Lato
// - The headings typeface, sans-serif
// - character set: Latin
// - served from Googlex
// - styles and weights: Bold, Black, 




// The font mixin
@mixin font($variant) {

  @if ($variant == 'roboto-light') {
    font-family: 'roboto', sans-serif;
    font-weight: 300;
    font-style: normal;
  }
  
  @if ($variant == 'roboto-regular') {
    font-family: 'roboto', sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  @if ($variant == 'roboto-medium') {
    font-family: 'roboto', sans-serif;
    font-weight: 500;
    font-style: normal;
  }
  
  @if ($variant == 'lato-bold') {
    font-family: 'lato', sans-serif;
    font-weight: 700;
    font-style: bold;
  }
  
  @if ($variant == 'lato-black') {
    font-family: 'lato', sans-serif;
    font-weight: 900;
    font-style: normal;
  }
  
}


// Font classes

.roboto-light {
  font-family: 'roboto', sans-serif;
  font-weight: 300;
  font-style: normal;
}

.roboto-regular {
  font-family: 'roboto', sans-serif;
  font-weight: 400;
  font-style: normal;
}

.roboto-medium {
  font-family: 'roboto', sans-serif;
  font-weight: 500;
  font-style: normal;
}

.lato-bold {
  font-family: 'lato', sans-serif;
  font-weight: 700;
  font-style: bold;
}

.lato-black {
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-style: normal;
}

