// Home page
//
// The homepage
// The header and footer are shared with other pages. See the default template.
//
// Styleguide home
html {
  &.home {
    body {
      @include home--gradients;

      .hero {
        @include hero;
        display: none;
      }

      .publishers {
        @include publishers;
      }

      .advertisers {
        @include advertisers;
      }

      .rich-media {
        @include rich-media;
      }

      .everything {
        @include everything;
      }

      .web-interface {
        @include web-interface;
      }

      .team {
        @include team;
      }
    }
  }
}
