@mixin popup {
  @include colors(white-on-black);

  position: fixed;
  top: 1vh;
  left: 1vw;
  width: 97vw;
  height: 98vh;
  z-index: 100;
  padding: $lem;
  box-sizing: border-box;

  display: flex;
  justify-content: center;
  align-items: center;

  display: none;
  opacity: 0;
  @include transition(opacity);

  &--active {
    @include popup--active;
  }

  .icon-hamburger {
    @include icon-hamburger;

    position: absolute;
    top: 1vw;
    right: 1vh;
  }
}
