@mixin footer-bottom {
  > * {
    @include text-centered;
  }

  .sprite--techstars {
    @include sprite($tech-stars);
  }

  &__made-with {
    @include footer-bottom__made-with;
  }

  .icon {
    @include icon($lem * 3, $lem * 3, $gray-footer);
  }

  @include footer-bottom--mobile;
  @include footer-bottom--tablet;
  @include footer-bottom--landscape;
}
