@mixin slider2--size($width, $margin, $button_width) {
  //$height: intrinsic ratio + figcaption
  $height: calc(#{$width} / 1.7778 + #{$lem * 2});
  $top: calc((#{$width} / 1.7778) / 2 - #{$button_width} / 4);

  height: $height;
  overflow: hidden;

  &__slides {
    .slide {
      width: $width;

      .img {
        height: calc(#{$width} / 1.7778);
      }

      @for $i from 1 to 11 {
        &:nth-of-type(#{$i}) {
          transform: translateX(($width + $margin) * ($i - 1));
        }
      }
    }
  }

  &__navigation {
    top: $top;

    .button {
      @include circle($button_width);

      .triangle {
        @include triangle($size: $button_width / 3, $color: $white);
      }
    }
  }
}
