@mixin everything {
  @include section-fitscreen;
  @include colors(white-on-green);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__hero {
    @include everything__hero;
  }

  .article {
    &__subtitle {
      padding: $lem / 4 0;
    }

    &__button {
      padding: $lem 0;

      .button {
        @include colors(white-on-red);
      }
    }
  }

  .articles {
    @include articles;
    @include section-margin;
    align-items: center;

    .article {
      @include colors(black-on-white);
      padding: $lem;

      &__icon {
        .icon {
          @include icon($lem * 2, $lem * 2, $green);
        }
      }

    }
  }
}
