@mixin article {
  display: flex;
  flex-direction: column;

  &__title {
    @include article__title;
  }

  &__subtitle {
    @include article__subtitle;
  }

  &__thumb {
    @include article__thumb;
  }

  &__excerpt {
    @include article__excerpt;
  }

  &__category {
    @include article__category;
  }

  &__button {
    @include article__button;
  }

  &__cta {
    @include article__cta;
  }

  &__icon {
    @include article__icon;
  }
}
