

// Text style
//
// Typographic settings for different text types.
// Each text type has set a `font`, a `scale`, a `color` and parameters like letter-spacing, max-width or else.
// Everything text uses this `text-style` mixin. We don't set `color` or `font` directly for a text.
//
// Styleguide text-style

// The width of a text block

$text-block-width: 25em; 



// The text mixin
@mixin text-style($variant) {
  
  // Default
  // - The basic settings for a text.
  @if ($variant == 'default') {
    @include font(roboto-regular);
    @include scale(default);
    @include colors(white-on-black);
    
    
    
  }
  
  // Body text
  // - Settings for long text blocks.
  @if ($variant == 'body-text') {
    @include p;
    
    max-width: 25em;
    
    
  }
  
  // Navigation
  // - Settings for navigation
  @if ($variant == 'navigation') {
    @include font(lato-black);
    @include scale(small-1);
    @include uppercase(1);
    
    
    
  }
  
  // Section title
  // - Settings for section titles
  @if ($variant == 'section-title') {
    @include font(lato-bold);
    @include uppercase(0.04em);
    
    
    
  }
  
  // Footer
  // - Settings for footer links
  @if ($variant == 'footer') {
    @include font(roboto-medium);
    @include scale(default);
    
    letter-spacing: 0.03em;
    
    
  }
  
  // Hero
  // - Settings for hero text
  @if ($variant == 'hero') {
    @include font(roboto-light);
    @include scale(large-3);
    
    line-height: 1.125;
    max-width: 15em;
    letter-spacing: 0.035em;
    
    
  }
  
  // Headline 2
  // - Settings for headlines level 2
  @if ($variant == 'headline-2') {
    @include font(roboto-regular);
    @include scale(large-2);
    
    line-height: 1.125;
    max-width: 15em;
    letter-spacing: -0.01em;
    
    
  }
  
  // Headline 3
  // - Settings for headlines level 3
  @if ($variant == 'headline-3') {
    @include font(roboto-regular);
    @include scale(large-1);
    
    letter-spacing: 1;
    line-height: 1.125;
    max-width: 15em;
    letter-spacing: -0.01em;
    
    
  }
  
}


// Text style classes

.text-style--default {
  @include font(roboto-regular);
  @include scale(default);
  @include colors(white-on-black);
  
  
  
}

.text-style--body-text {
  @include p;
  
  max-width: 25em;
  
  
}

.text-style--navigation {
  @include font(lato-black);
  @include scale(small-1);
  @include uppercase(1);
  
  
  
}

.text-style--section-title {
  @include font(lato-bold);
  @include uppercase(0.04em);
  
  
  
}

.text-style--footer {
  @include font(roboto-medium);
  @include scale(default);
  
  letter-spacing: 0.03em;
  
  
}

.text-style--hero {
  @include font(roboto-light);
  @include scale(large-3);
  
  line-height: 1.125;
  max-width: 15em;
  letter-spacing: 0.035em;
  
  
}

.text-style--headline-2 {
  @include font(roboto-regular);
  @include scale(large-2);
  
  line-height: 1.125;
  max-width: 15em;
  letter-spacing: -0.01em;
  
  
}

.text-style--headline-3 {
  @include font(roboto-regular);
  @include scale(large-1);
  
  letter-spacing: 1;
  line-height: 1.125;
  max-width: 15em;
  letter-spacing: -0.01em;
  
  
}

