@mixin team {
  @include section-fitscreen;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__hero {
    @include team__hero;
  }

  .articles {
    @include articles(4);
    @include section-margin;
    align-items: center;

    .article {
      > * {
        order: 3;
      }

      &__subtitle {
        padding: $lem / 4 0;
        order: 1;
      }

      &__category {
        order: 2;
      }

      &__excerpt {
        padding: $lem 0;
      }
    }
  }
}
