@mixin publishers__mockups--portrait {
  @include portrait {
    position: relative;

    .mockup {
      position: absolute;
      left: 0;
      top: 0;

      &:first-of-type {
        width: $lem * 20;
        transform: scale(.7) translate(-$lem * 5, $lem * 5);
        z-index: 1;
      }

      &:last-of-type {
        width: $lem * 70;
        perspective: 1000px;

        .mockup--browser {
          transform: rotateY(-10deg);
        }
      }
    }
  }
}
