@mixin publishers {
  @include section-padding;
  @include section-fitscreen;

  display: flex;
  flex-wrap: wrap;
  align-items: center;

  .articles {
    @include publishers__articles;
  }

  &__mockups {
    @include publishers__mockups;
  }

  &__slider {
    @include publishers__slider;
  }

  @include publishers--portrait;
  @include publishers--landscape;
}
