@mixin advertisers--landscape {
  @include landscape {
    justify-content: space-between;

    .articles {
      width: $text-block-width;
      margin-left: $lem;
      order: 2;
    }

    .advertisers__mockups {
      width: calc(100% - #{$text-block-width} - #{$lem});
      max-width: 55vw;
      min-height: 50vh;
      order: 1;
    }

    .advertisers__slider {
      order: 3;
    }
  }
}
