@mixin web-interface__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .article {
    @include article;

    &__subtitle {
      padding: $lem / 4 0;
    }

    &__button {
      padding: $lem / 2 0;

      .button {
        @include colors(white-on-red);
      }
    }

    &:last-of-type {
      margin-top: 5vh;

      .article__cta {
        order: 1;
      }

      .article__button {
        order: 2;
      }
    }
  }
}
