// Colors
//
// The colors together with the color pairs.
// Colors don't exist alone yet in pairs, like black on white.
// All color pairs have a contrast ratio set for perfect readability using [Lea Verou's contrast checking tool](http://leaverou.github.io/contrast-ratio/).
//
// Styleguide colors

// Main color
$white: #fff;

// Main color
$black: #292d31;

// Main color
$blue: #00c0d8;

// Main color
$green: #2fe79f;

// Main color
$green-dark: #2c9971;

// Secondary color
$red: #f7425a;

// Secondary color
$gray-footer: #7c8691;

// Secondary color
$gray-team: #8b95a1;

// Secondary color
$gray-format: #e5e5e5;


// Color combinations. Colors are not used standalone but in pairs
@mixin colors($name) {

  @if ($name == 'white-on-transparent') {
    color: $white;
    background-color: transparent;
    
  }

  @if ($name == 'gray-format-on-transparent') {
    color: $gray-format;
    background-color: transparent;
    
  }

  @if ($name == 'green-on-transparent') {
    color: $green;
    background-color: transparent;
    
  }

  @if ($name == 'black-on-white') {
    color: $black;
    background-color: $white;
    
  }

  @if ($name == 'gray-footer-on-white') {
    color: $gray-footer;
    background-color: $white;
    
  }

  @if ($name == 'white-on-red') {
    color: $white;
    background-color: $red;
    
  }

  @if ($name == 'white-on-green') {
    color: $white;
    background-color: $green;
    
  }

  @if ($name == 'white-on-blue') {
    color: $white;
    background-color: $blue;
    
  }

  @if ($name == 'white-on-black') {
    color: $white;
    background-color: $black;
    
  }

  @if ($name == 'gray-team-on-black') {
    color: $gray-team;
    background-color: $black;
    
  }

  @if ($name == 'blue-on-white') {
    color: $blue;
    background-color: $white;
    
  }

  @if ($name == 'gray-footer-on-white') {
    color: $gray-footer;
    background-color: $white;
    
  }

}

// Color classes

.white {
  background: #fff;
};

.black {
  background: #292d31;
};

.blue {
  background: #00c0d8;
};

.green {
  background: #2fe79f;
};

.green-dark {
  background: #2c9971;
};

.red {
  background: #f7425a;
};

.gray-footer {
  background: #7c8691;
};

.gray-team {
  background: #8b95a1;
};

.gray-format {
  background: #e5e5e5;
};


// Color pair classes

.white-on-transparent {
  color: $white;
  background-color: transparent;
  
}

.gray-format-on-transparent {
  color: $gray-format;
  background-color: transparent;
  
}

.green-on-transparent {
  color: $green;
  background-color: transparent;
  
}

.black-on-white {
  color: $black;
  background-color: $white;
  
}

.gray-footer-on-white {
  color: $gray-footer;
  background-color: $white;
  
}

.white-on-red {
  color: $white;
  background-color: $red;
  
}

.white-on-green {
  color: $white;
  background-color: $green;
  
}

.white-on-blue {
  color: $white;
  background-color: $blue;
  
}

.white-on-black {
  color: $white;
  background-color: $black;
  
}

.gray-team-on-black {
  color: $gray-team;
  background-color: $black;
  
}

.blue-on-white {
  color: $blue;
  background-color: $white;
  
}

.gray-footer-on-white {
  color: $gray-footer;
  background-color: $white;
  
}

